
 .project-image {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
  }
  
  .relative {
    position: relative;
  }
  
  .flex {
    display: flex;
  }
  
  .items-center {
    align-items: center;
  }
  
  .absolute {
    position: absolute;
  }
  
  .left-0 {
    left: 0;
  }
  
  .ml-4 {
    margin-left: 1rem;
  }
  
  .text-black {
    color: black;
  }
  
  .focus\:outline-none:focus {
    outline: none;
  }
  
  .work-history-wrapper {
    overflow: hidden;
    opacity: 0;
    transition: max-height 0.5s ease, opacity 0.5s ease;
  }
  
  .work-history-wrapper.show { /* Adjust this based on your content's height */
    opacity: 1;
    height: auto;
    background-color: white;
  }
  
  .rotate-180 {
    transform: rotate(180deg);
  }
  