.work-history-container {
    font-family: Arial, sans-serif;
    padding: 20px;
     overflow: hidden;
    transition: opacity 0.5s ease, max-height 0.5s ease;
  }
  
  .work-history-container.show {
    opacity: 1;
    max-height: 500px; /* Adjust based on your content's height */
  }
  
  .logo {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .logo img {
    max-width: 200px;
  }
  
  .responsibilities {
    margin-top: 20px;
  }
  
  .responsibilities h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .responsibilities ul {
    list-style: disc;
    padding-left: 20px;
  }
  